import { Injectable } from '@angular/core';
import { HttpClient, HttpEventType, HttpErrorResponse } from '@angular/common/http';
import { AvlUploadService, AvlFile, AvlAbortUploadAction, AvlUploadProgressEvent, AvlHttpErrorResponse } from '@avl-controls/interfaces';

import { map, catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Injectable()
export class UploadService implements AvlUploadService {
  constructor(private httpClient: HttpClient) { }

  downloadFile(url: string): Promise<Blob> {
    return this.httpClient.get(url, { responseType: "blob" }).toPromise();
  }

  uploadFile(file: AvlFile, target: string, progressCallback?: (progress: AvlUploadProgressEvent) => void, completeCallback?: (downloadUrl?: string) => void, failCallback?: (errorResponse: AvlHttpErrorResponse) => void)
  : AvlAbortUploadAction {
    const formData = new FormData();
    formData.append('file', file.file);
    const uploadReq: Observable<any> = this.httpClient.post<{downloadUrl?: string}>(target, formData, { reportProgress: true, observe: 'events' } )
      .pipe(map(event => {
        if(event.type === HttpEventType.UploadProgress && progressCallback) {
          progressCallback(event);
        }
        if(event.type === HttpEventType.Response) {
          if(completeCallback && event.body != null && event.body.downloadUrl) {
            completeCallback(event.body.downloadUrl);
          }
          else if(completeCallback) completeCallback();
        }
      }), catchError( (err: HttpErrorResponse, caught) => {
        const response = err.error instanceof Event ? (<XMLHttpRequest>err.error.target).response : err.error;
        if(failCallback) failCallback({
          status: err.status,
          response: response
        });
        return new Observable(null);
      }));
      const subscription = uploadReq.subscribe();
      return subscription.unsubscribe.bind(subscription);
  }
}
